<template>
  <div class="item_list accordion-section-title">
      <div class="below"  @click="edit_setting(id)">
          <span class="title">{{item.title}} </span>
          <div class="arrow_right">
              <img src="https://www.shareicon.net/data/2015/10/06/113447_right_512x512.png"/>
          </div>
      </div>
         <div  :class='["deep deep_child",id==id_check_edit?"setting_edit_theme":""]'>
          <ul class="customize-pane-child accordion-sub-container control-panel-content accordion-section control-section control-panel control-panel-widgets" id="sub-accordion-panel-widgets"  >
            <li class="panel-meta customize-info accordion-section ">
               
                <span class="screen-reader-text"  @click="edit_setting">  <img src="https://www.pngrepo.com/png/304501/512/arrow-left.png"/></span>
                 <div class="right_deep">
                 <p class="desc_edit">Äang sá»­a</p>
                 <p class="title_edit">{{item.title}}</p>
                </div>
            
            </li>
              <li id="customize-control-blogname"  class="customize-control customize-control-text">
                  <label for="_customize-input-blogname" class="customize-control-title"> Title</label>
           <input v-on:input="(event,...args)=>this.CHANGE_STATUS_PRODUCT([id, event.target.value,'title',...args])" class="customize-input-blogname" type="text" :value="item.title" data-customize-setting-link="blogname">
              </li>
              <li id="customize-control-blogname"  class="customize-control customize-control-text">
                  <label for="_customize-input-blogname" class="customize-control-title"> Price</label>
                   <input v-on:input="(event,...args)=>this.CHANGE_STATUS_PRODUCT([id, event.target.value,'price',...args])" class="customize-input-blogname" type="text" :value="item.price" data-customize-setting-link="blogname">
              </li>
              <li id="customize-control-blogname"  class="customize-control customize-control-text">
                  <label for="_customize-input-blogname" class="customize-control-title"> Favoriotes</label>
                  <input v-on:input="(event,...args)=>this.CHANGE_STATUS_PRODUCT([id, event.target.value,'favoriotes',...args])" class="customize-input-blogname" type="text" :value="item.favoriotes" data-customize-setting-link="blogname">
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
import { ref } from "vue";

export default{
  name:"SettingItemChildProduct", 
  props:[ 'item' ,'id','id_check_edit'],
  methods:{

    CHANGE_STATUS_PRODUCT(args){ 
      this.args = args

      this.$store.commit('CHANGE_STATUS_PRODUCT',args)
    }
  },
 setup(props,context){
    const isEditSetting = ref(false);
    
   const edit_setting = (value) =>{
      context.emit('truyen_xuong',value)
      isEditSetting.value = !isEditSetting.value
     }


  return {
    edit_setting,
    isEditSetting  
  }
}
}
</script>

<style scoped>
  .deep.deep_child{
        top: 0;
  }
</style>