<template>
  <div>
    <div class="below" @click="edit_setting">
      <span class="title">Products</span>
      <div class="arrow_right">
        <img
          src="https://www.shareicon.net/data/2015/10/06/113447_right_512x512.png"
        />
      </div>
    </div>
    <div :class="['deep', isEditSetting ? 'setting_edit_theme' : '']">
      <div v-for="(item, index) in products" :key="index">
        <SettingItemChildProduct :item="item" :id="index" 
           :id_check_edit="check_edit_value"
           v-on:truyen_xuong="edit_setting_child" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { ref } from "vue";
import SettingItemChildProduct from "./SettingItemChildProduct.vue";
export default {
  name: "SettingItem",
  computed: {
    ...mapState(["products"]),
  },
  methods: {},
  components: { SettingItemChildProduct },
  setup() {
    const isEditSetting = ref(false);
     const check_edit_value = ref("");
     const edit_setting = () => {
           isEditSetting.value = !isEditSetting.value;
            // check_edit_value.value = id_check;
        };
     const edit_setting_child = (id_check) => {
          console.log(id_check);
            // isEditSetting.value = !isEditSetting.value;
            check_edit_value.value = id_check;
        };

    return {
      isEditSetting,
      edit_setting,
      check_edit_value,
      edit_setting_child
    };
  },
};
</script>

