<template>
    <div :class="{ 'market-setting_content': true, setting_theme: isSetting }">
        <div class="top">
            <div class="left" @click="change_setting">
                <img
                    src="https://cdn.iconscout.com/icon/free/png-256/close-1912235-1617704.png"
                />
            </div>
            <div class="right" @click="change_setting">
                <button class="save">LÆ°u</button>
            </div>
        </div>
        <div class="center">
            <div
                class="item_list accordion-section-title"
                v-for="(item, index) in themes"
                :key="index"
            >
                <SettingItem
                    :id_check_edit="check_edit_value"
                    :item="item"
                    v-on:truyen_xuong="edit_setting"
                    :isEditSetting="isEditSetting"
                />
            </div>
            <div
                class="item_list accordion-section-title"
               
            >
                <SettingItemProduct />
            </div>
        </div>
    </div>
    <div class="market-setting" @click="change_setting">
        <h3>Setting!</h3>
        <p
            class="background_setting"
            :style="{
                backgroundImage:
                    'url(http://primedepartamentos.com/images/icons/settings-icon.png )',
            }"
        ></p>
    </div>
</template>

<script>
import { ref } from "vue";
import SettingItem from "./SettingItem";
import SettingItemProduct from "./SettingItemProduct";
import { mapState, useStore, mapActions } from "vuex";

export default {
    name: "Banner",
    props: [],
    components: {
        SettingItem,
        SettingItemProduct,
    },
    computed: {
        ...mapState(["themes"]),
    },
    methods: {
        ...mapActions(["getTodosAsyn"]),
    },

    setup() {
        const isSetting = ref(false);
        const check_edit_value = ref("");
        const isEditSetting = ref(false);
        const store = useStore();
        store.dispatch("getTodosAsyn");

        const change_setting = () => {
            isSetting.value = !isSetting.value;
        };

        const edit_setting = (id_check) => {
            isEditSetting.value = !isEditSetting.value;
            check_edit_value.value = id_check;
        };
        return {
            isSetting,
            change_setting,
            isEditSetting,
            edit_setting,
            check_edit_value,
        };
    },
};
</script>

