<template>
   <div class="market-product">
      <div class="market-product__container container_common_normal">
        <div class="item_product"  v-for="(value, index) in  products"  :key="index" >
          <ProductItem
           :item="value"
          />
        </div>
      
      </div>
      <button class="button">See more</button>
   </div>
</template>

<script>
  import { mapState, useStore, mapActions } from "vuex";
  import ProductItem from './ProductItem'
export default {
  name: 'Product',

   components:{ProductItem},
    computed: {
        ...mapState(["products"]),
    },
    methods: {
        
        ...mapActions(["getProductsAsyn"]),
    },
    setup() {
         const store = useStore();
        store.dispatch("getProductsAsyn");
    },
}
</script>

