import { createStore } from 'vuex'
import axios from 'axios'
const URL_REQUEST = "https://my-json-server.typicode.com/vovanlong96/market_fresher";

// import { createStore } from "vuex";
// import axios from "axios";
// const URL_REQUEST = "https://jsonplaceholder.typicode.com/todos";
export const store = createStore({
  state: {
    //khá»i táº¡o

   
    themes:[],
     best_products:[],
     products:[]
  },
  getters: {
    // tÃ­nh toÃ¡n

     
  },

  mutations: {
    // sá»± kiá»n
    
    GET_LIST_THEMES: (state, data) => {

      var result = Object.entries( data) 
       state.themes = result;
    },
    GET_LIST_best_products: (state, data) => {
      
       state.best_products = data;
    },
    GET_LIST_products: (state, data) => {
      console.log(data);
      // var result = Object.entries( data) 
       state.products = data;
    },
     CHANGE_STATUS:(state,args)=>{ 
      // let position = args[0];
       const id = args[1].trim();
      
      // let value = args[2];
        state.themes.map((item)=>{
          // console.log(item[0]);
          if(item[0]===args[0]){
             
           item[1][id]  = args[2]
          }
        return item;
        })
        
    },
     CHANGE_STATUS_PRODUCT:(state,args)=>{ 
      // let position = args[0];
      
       const id = args[2].trim();

      // let value = args[2];
        state.products.map((item,index)=>{
          // console.log(item[0]);
          if(index===args[0]){
             
            item[id]  = args[1]
          }
        return item;
        })
      
    },
  },
  actions: {
    // sá»± kiá»n báº¥t Äá»ng bá» thÃ´ng qua axios vÃ  mutations
  
    async getTodosAsyn(context) {
      try {
        await axios.get(URL_REQUEST + "/themes").then((response) => {

          context.commit("GET_LIST_THEMES", response.data);
        });

      } catch (error) {
        console.log(error);
      }
    },
    async getProductBestAsyn(context) {
      try {
        await axios.get(URL_REQUEST + "/best_products").then((response) => {

          context.commit("GET_LIST_best_products", response.data);
        });
        
      } catch (error) {
        console.log(error);
      }
    },
    async getProductsAsyn(context) {
      try {
        await axios.get(URL_REQUEST + "/products").then((response) => {

          context.commit("GET_LIST_products", response.data);
        });
        
      } catch (error) {
        console.log(error);
      }
    },
  },
});
