<template>
  <div>
      <div class="below"  @click="edit_setting(item[0])">
          <span class="title">{{item[0].replace('_', ' ')}}</span>
          <div class="arrow_right">
              <img src="https://www.shareicon.net/data/2015/10/06/113447_right_512x512.png"/>
          </div>
      </div>
      <div  :class='["deep",item[0]===id_check_edit?"setting_edit_theme":""]'>
          <ul class="customize-pane-child accordion-sub-container control-panel-content accordion-section control-section control-panel control-panel-widgets" id="sub-accordion-panel-widgets"  >
            <li class="panel-meta customize-info accordion-section ">
               
                <span class="screen-reader-text"  @click="edit_setting">  <img src="https://www.pngrepo.com/png/304501/512/arrow-left.png"/></span>
                 <div class="right_deep">
                 <p class="desc_edit">Äang sá»­a</p>
                 <p class="title_edit">{{item[0].replace('_', ' ')}}</p>
                </div>
            
            </li>
              <li id="customize-control-blogname" v-for="(value,index) in item[1]" :key="index"  class="customize-control customize-control-text">
             

                  <label for="_customize-input-blogname" class="customize-control-title"> {{index.replace('_', ' ')}}</label>
                  <input 
                  v-on:input="(event,...args)=>this.CHANGE_STATUS([item[0],index,event.target.value,...args])"
                   v-if="index==='color' ||index==='background' || value.indexOf('#') > -1"   type="color"   :value="value">
                  <input  v-on:input="(event,...args)=>this.CHANGE_STATUS([item[0],index,event.target.value,...args])" class="customize-input-blogname" type="text" :value="value" data-customize-setting-link="blogname">
                  <input v-if="item[0]=='font_size'"
                  v-on:input="(event,...args)=>this.CHANGE_STATUS([item[0],index,event.target.value+'px',...args])"
                   type="range"  :value="value.replace('px', '')" name="points" min="0" max="100">
              </li>
          </ul>
      </div>
  </div>
</template>

<script>

export default{
  name:"SettingItem", 
    props:['isEditSetting','item','id_check_edit'],
     methods:{

      CHANGE_STATUS(args){ 
          this.args = args
 
        this.$store.commit('CHANGE_STATUS',args)
      }
    },
    setup(props,context){

      const edit_setting = (value) =>{
      context.emit('truyen_xuong',value)
     }
    
       return {
      edit_setting 
     }


    
    

  }
}
</script>

