    }
     function isMobile() {
       if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
         
         return true
       } else {
        is_menu.value =true;
         return false
       }
     }
     isMobile();

    
    return {
      is_menu, 
      change_menu_mobi,
      
    }
  },

};
